body,
h1,
h3,
input {
  padding: 0;
  margin: 0;
  outline: none;
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  color: #666;
}
h1,
h3 {
  padding: 12px 0;
  font-weight: 400;
}
h1 {
  font-size: 28px;
}
.main-block,
.info {
  display: flex;
  flex-direction: column;
}
.main-block {
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;

  background-size: cover;
}
form {
  width: 86%;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: solid 1px #ccc;
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.31);
  background: #ebebeb;
}
.info-item {
  width: 100%;
}
input {
  width: calc(100% - 70px);
  height: 40px;
  padding-left: 17px;
  margin: 0 0 12px -4px;
  border-radius: 0 5px 5px 0;
  border: solid 1px #cbc9c9;
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.09);
  background: #fff;
}
.icon {
  padding: 9px 15px;
  margin-top: -1px;
  border-radius: 5px 0 0 5px;
  border: solid 0px #cbc9c9;
  background: rgb(35, 65, 155, 0.8);
  color: #fff;
}
input[type="radio"] {
  display: none;
}
label.radio {
  position: relative;
  display: inline-block;
  text-indent: 32px;
  cursor: pointer;
}
label.radio:before {
  content: "";
  position: absolute;
  left: 0;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: 0.5px solid rgb(35, 65, 155, 0.8);
  background: #fff;
}
label.radio:after {
  content: "";
  position: absolute;
  width: 8px;
  height: 4px;
  top: 5px;
  left: 4px;
  border-bottom: 3px solid rgb(35, 65, 155, 0.8);
  border-left: 3px solid rgb(35, 65, 155, 0.8);
  transform: rotate(-45deg);
  opacity: 0;
}
input[type="radio"]:checked + label:after {
  opacity: 1;
}
textarea {
  width: 99%;
  margin-bottom: 12px;
}
button {
  width: 100%;
  padding: 8px;
  border-radius: 5px;
  border: none;
  background: rgb(35, 65, 155, 0.8);
  font-size: 14px;
  font-weight: 600;
  color: #fff;
}
button:hover {
  background: rgb(35, 65, 155, 0.8);
}
.grade-type div {
  display: flex;
  margin: 6px 0;
}
